import type { ViewName } from '../views';
export interface Item {
  id: string;            // ÂîØ‰∏ÄÊ†áËØÜ: filePath#line
  title: string;         // Ê†áÈ¢ò
  content: string;       // ÂÖ®ÈÉ®ÂÜÖÂÆπÔºà‰ªªÂä°Êï¥Ë°åÊàñÂùóÂÖ®ÊñáÔºâ
  type: 'task' | 'block';// Á±ªÂûãÔºö‰ªªÂä° Êàñ ÊñáÊú¨Âùó
  status?: string;       // Áä∂ÊÄÅÔºö‰ªªÂä°=open/done/cancelledÔºåÂùó=Áä∂ÊÄÅ:: ÂÄºÔºàËã•Êó†Âàô undefinedÔºâ
  category: string;      // Á±ªÂà´ÔºöÂàÜÁ±ª::/Á±ªÂà´:: ÂÄºÊàñÁà∂Êñá‰ª∂Â§πÂêç
  tags: string[];        // Ê†áÁ≠æÂàóË°®Ôºö‰ªªÂä°Ë°åÂÜÖ#tagÔºåÂùó‰∏ªÈ¢ò:: ÊãÜÂàÜ
  recurrence: string;    // ÈáçÂ§çËßÑÂàôÔºöÂ¶Ç "every day when done"ÔºåÂê¶Âàô 'none'
  date?: string;         // Êó•ÊúüÔºö‰ªªÂä°ÂÆåÊàê/ÂèñÊ∂àÊó•Êúü Êàñ Âùó Êó•Êúü:: ÂÄºÔºàÊ†ºÂºè YYYY-MM-DDÔºâ
  created: number;       // ÂàõÂª∫Êó∂Èó¥ÔºàÊñá‰ª∂Á∫ßÔºåUnix ms Êó∂Èó¥Êà≥Ôºâ
  modified: number;      // ‰øÆÊîπÊó∂Èó¥ÔºàÊñá‰ª∂Á∫ßÔºåUnix ms Êó∂Èó¥Êà≥Ôºâ
  extra: Record<string, string|number|boolean>;  // ÂÖ∂‰ªñÈîÆÂÄºÂØπÔºåÂ¶Ç Êó∂Èó¥/Êó∂Èïø/‰ºòÂÖàÁ∫ß/ÂõæÊ†á Á≠â
  header?: string;           // ÊâÄÂú®ÁöÑ Markdown Ê†áÈ¢òÊñáÊú¨
  icon?: string;             // ÂõæÊ†á emoji
  priority?: 'lowest' | 'low' | 'medium' | 'high' | 'highest';  // ‰ºòÂÖàÁ∫ß
  createdDate?: string;      // ÂàõÂª∫Êó•Êúü ‚ûï
  scheduledDate?: string;    // ÂÆâÊéíÊó•Êúü ‚è≥
  startDate?: string;        // ÂºÄÂßãÊó•Êúü üõ´
  dueDate?: string;          // Êà™Ê≠¢Êó•Êúü üìÖ
  doneDate?: string;         // ÂÆåÊàêÊó•Êúü ‚úÖ
  cancelledDate?: string;    // ÂèñÊ∂àÊó•Êúü ‚ùå
  filename?: string;         // Êñá‰ª∂Âêç
}

/** ‰ª™Ë°®ÁõòÊ®°ÂùóÈÖçÁΩÆ */
export interface ModuleConfig {
  view: ViewName;
  title: string;
  collapsed?: boolean;
  filters?: FilterRule[];
  sort?: SortRule[];
  group?: string;
  props?: Record<string, any>;
  fields?: string[];
}

/** ËøáÊª§Êù°‰ª∂ */
export interface FilterRule {
  field: string;   // ÂèØ‰∏∫ extra.xxx
  op: '=' | '!=' | 'includes' | 'regex' | '>' | '<';
  value: any;
}

/** ÊéíÂ∫èÊù°‰ª∂ */
export interface SortRule {
  field: string;   // ÂèØ‰∏∫ extra.xxx
  dir: 'asc' | 'desc';
}

/** ‰ª™Ë°®ÁõòÈÖçÁΩÆ */
export interface DashboardConfig {
  name: string;
  path?: string;
  tags?: string[];
  initialView?: string;
  initialDate?: string;
  modules: ModuleConfig[];
}

/* ================================
 *  Â≠óÊÆµÁªü‰∏ÄÁª¥Êä§ & Â∑•ÂÖ∑ÂáΩÊï∞
 * ================================ */

/** ‰∏ÄÂ§ÑÁª¥Êä§ÁöÑÊ†∏ÂøÉÂ≠óÊÆµÊ∏ÖÂçïÔºàÈùû extra.*Ôºâ */
export const CORE_FIELDS = [
  'id','title','content','type','status','category','tags','recurrence',
  'date','header','icon','priority','createdDate','scheduledDate','startDate',
  'dueDate','doneDate','cancelledDate','created','modified','filename'
] as const;

export type CoreField = typeof CORE_FIELDS[number];

/** ‰ªé items ÁîüÊàê ‚ÄúÊ†∏ÂøÉÂ≠óÊÆµ + extra.xxx‚Äù ÁöÑÂÆåÊï¥Â≠óÊÆµÂàóË°® */
export function getAllFields(items: Item[]): string[] {
  const set = new Set<string>(CORE_FIELDS as unknown as string[]);
  for (const it of items) {
    Object.keys(it.extra || {}).forEach(k => set.add('extra.' + k));
  }
  return Array.from(set);
}

/** ÈÄöÁî®ËØªÂèñÂ≠óÊÆµÂÄºÔºàÊîØÊåÅ extra.xxxÔºâ */
export function readField(item: Item, field: string): any {
  if (field.startsWith('extra.')) {
    const key = field.substring(6);
    return item.extra?.[key];
  }
  // @ts-ignore
  return item[field as keyof Item];
}
