// Think Plugin ÂÖ•Âè£ÔºàÂÆåÊï¥ÂÆûÁé∞ÔºåÂåÖÂê´ openSettingsForDashboardÔºâ
//-----------------------------------------------------------

import { Plugin } from 'obsidian';
import { DataStore } from './data/store';
import { DashboardConfig } from './config/schema';
import { SettingsTab } from './ui/SettingsTab';
import { STYLE_TAG_ID, GLOBAL_CSS } from './config/constants';
import { VaultWatcher } from './core/VaultWatcher';
import { CodeblockEmbedder } from './core/CodeblockEmbedder';

export interface PersistData {
  dashboards: DashboardConfig[];
  inputSettings?: Record<string, any>;
}

export default class ThinkPlugin extends Plugin {
  /* ---------- Â±ûÊÄß ---------- */
  dataStore!: DataStore;                                          // ÂÖ®Â±ÄÊï∞ÊçÆÂ±Ç
  dashboards: DashboardConfig[] = [];                             // ÊâÄÊúâ‰ª™Ë°®Áõò
  activeDashboards: Array<{ container: HTMLElement; configName: string }> = [];
  inputSettings: Record<string, any> = {};                        // ÈÄöÁî®ÊåÅ‰πÖÂåñ
  currentSettingsTarget: string | null = null;                    // ÊâìÂºÄËÆæÁΩÆÊó∂ËÅöÁÑ¶ÁöÑ‰ª™Ë°®ÁõòÂêç

  /* ---------- ÁîüÂëΩÂë®Êúü ---------- */
  async onload() {
    console.log('ThinkPlugin Âä†ËΩΩ - ÈáçÊûÑÁâà');

    /* ËØªÂèñÊåÅ‰πÖÂåñ */
    const saved = (await this.loadData()) as PersistData | undefined;
    if (saved?.dashboards) this.dashboards = saved.dashboards;
    if (!this.dashboards.length) this._createDefaultDashboard();
    this.inputSettings = saved?.inputSettings ?? {};

    /* ÂêëÂêéÂÖºÂÆπÊóßËßÜÂõæÂêç */
    this.dashboards.forEach(d =>
      d.modules.forEach(m => {
        if ((m.view as any) === 'ListView') m.view = 'BlockView';
      }),
    );

    /* Êï∞ÊçÆÂ±Ç & ÁõëÂê¨ */
    this.dataStore = new DataStore(this.app);
    await this.dataStore.scanAll();
    new VaultWatcher(this, this.dataStore);
    new CodeblockEmbedder(this, this.dataStore);

    /* Ê†∑ÂºèÊ≥®ÂÖ• + ËÆæÁΩÆÈ°µ */
    this._injectStyles();
    this.addSettingTab(new SettingsTab(this.app, this));

    // ‚ñº Ëã•Â≠òÂú®ÂæÖÂ±ïÂºÄÁõÆÊ†áÔºåÂú® SettingsTab.display ‰∏≠Â§ÑÁêÜ
    this.register(() => localStorage.removeItem('think-target-dash'));
  }

  onunload() {
    document.getElementById(STYLE_TAG_ID)?.remove();
    console.log('ThinkPlugin Âç∏ËΩΩ');
  }

  /* ---------- ÂÖ¨ÂÖ± API ---------- */

  /**
   * ‰ªé Dashboard ËßÜÂõæË∞ÉÁî®ÔºöÁ´ãÂç≥ÊâìÂºÄÊèí‰ª∂ËÆæÁΩÆÂπ∂Ëá™Âä®Â±ïÂºÄÂØπÂ∫î‰ª™Ë°®Áõò
   */
  openSettingsForDashboard(name: string) {
    // ‚ë† ÂÖàËÆ∞Âà∞ localStorageÔºåÈò≤Ê≠¢Âêå‰∏Ä session Â§öÂÆû‰æã
    localStorage.setItem('think-target-dash', name);

    // ‚ë° ÊâìÂºÄËÆæÁΩÆÈù¢ÊùøÔºåÂÜçÂàáÊç¢Âà∞Êú¨Êèí‰ª∂
    this.app.setting.open();                    // Obsidian >=1.4
    this.app.setting.openTabById(this.manifest.id);
  }

  /**
   * ‰øùÂ≠òÂÖ®ÈÉ®‰ª™Ë°®Áõò‰∏éÁî®Êà∑ËæìÂÖ•ÈÖçÁΩÆ
   */
  async persistAll() {
    await this.saveData({
      dashboards: this.dashboards,
      inputSettings: this.inputSettings,
    });
  }

  /* ---------- ÂÜÖÈÉ®Â∑•ÂÖ∑ ---------- */

  /** ÈªòËÆ§Á§∫‰æã‰ª™Ë°®ÁõòÔºàÈ¶ñÊ¨°ÂÆâË£ÖÊó∂Ê≥®ÂÖ•Ôºâ */
  private _createDefaultDashboard() {
    const now = new Date();
    const today = `${now.getFullYear()}-${String(now.getMonth() + 1).padStart(
      2,
      '0',
    )}-${String(now.getDate()).padStart(2, '0')}`;

    const def: DashboardConfig = {
      name: 'ÈªòËÆ§‰ª™Ë°®Áõò',
      path: '',
      tags: [],
      initialView: 'Êúà',
      initialDate: today,
      modules: [
        {
          view: 'BlockView',
          title: 'Êú™ÂÆåÊàê‰ªªÂä°',
          filters: [
            { field: 'type', op: '=', value: 'task' },
            { field: 'status', op: '=', value: 'open' },
          ],
          sort: [],
          collapsed: false,
          props: {},
        },
        {
          view: 'BlockView',
          title: 'Â∑≤ÂÆåÊàê‰ªªÂä°',
          filters: [
            { field: 'type', op: '=', value: 'task' },
            { field: 'status', op: '=', value: 'done' },
          ],
          sort: [{ field: 'date', dir: 'desc' }],
          collapsed: true,
          props: {},
        },
      ],
    };
    this.dashboards.push(def);
  }

  /** ÊääÂÖ®Â±Ä CSS Ê≥®ÂÖ•Âà∞ÊñáÊ°£ <head> */
  private _injectStyles() {
    if (document.getElementById(STYLE_TAG_ID)) return;
    const style = document.createElement('style');
    style.id = STYLE_TAG_ID;
    style.textContent = GLOBAL_CSS;
    document.head.appendChild(style);
  }
}